.wiget{
    display: flex;
    flex: 1;
    padding: 10px;
    justify-content: space-between;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47); 
    box-shadow: 2px 4px 10px 1px rgba(201,201,201, 0.47); 
    border-radius: 10px;
    height: 100px;
    .left , .right{
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .users{
            font-size: 14px;
            font-weight: bold;
            color: rgb(173, 173, 173);
        }
        .counter{
            font-size: 28px;
            font-weight: 300;

        }
        .link{
            font-size: 12px;
            border-bottom: 1px solid gray;
            width: max-content;
            cursor: pointer;
        }
        .persentage{
            display: flex;
            align-items: center;
            font-size: 14px;
            &.positive{
                color: green;
            }
            &.nagetive{
                color: red;
            }
        }
        .icon{
            align-self: flex-end;
            padding: 5px;
            border-radius: 5px;
        }
    }
}
