.single{
    display: flex;
    width: 100%;
    .singleContainer{
        flex: 6;
        .top{
            display: flex;
            padding: 20px;
            gap: 20px;
            .left{
                flex: 1;
                -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47); 
                box-shadow: 2px 4px 10px 1px rgba(201,201,201, 0.47); 
                padding: 20px;
                position: relative;
                .editBtn{
                    position: absolute;
                    top: 0;
                    right: 0;
                    padding:5px;
                    border-radius: 0px 0px 0px 5px;
                    color: #7451f8;
                    background-color:#7551f81e;
                    cursor: pointer;
                    font-size: 12px;
                }
                .item{
                    display: flex;
                    gap: 20px;
                    .itemImg{
                        width: 100px;
                        height: 100px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    .detailes{
                        .itemTitle{
                            margin-bottom: 10px;
                            color: #555;
                        }
                        .detaileItem{
                            margin-bottom: 10px;
                            font-size: 14px;
                            .itemKey{
                                font-weight: bold;
                                color: gray;
                                margin-right: 5px;
                            }
                            .itemValue{
                                font-weight: 300;
                            }
                        }
                    }
                }
                .title{
                    font-size: 16px;
                    color: lightgray;
                    margin-bottom: 20px;
                }
            }
            .right{
                flex: 2;
            }
        }
        .bottom{
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47); 
                box-shadow: 2px 4px 10px 1px rgba(201,201,201, 0.47); 
                padding: 20px;
                margin: 10px 20px;
            .title{
                color: lightgray;
                font-size: 16px;
                margin-bottom: 20px;
            }
        }
    }
}